* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
}

button {
  background: none;
  border: none;
}

body {
  font-family: "Noto Sans KR", system-ui, -apple-system, sans-serif;
  background: #d9d9d9;
  color: #111;
  min-height: 100vh;
}

.wrap {
  max-width: 720px;
  margin: 0 auto;
  padding: 48px 16px 120px;
}

h2 {
  text-align: center;
  font-size: 30px;
  font-weight: 800;
  margin-bottom: 30px;
}

.form {
  display: flex;
  gap: 14px;
  align-items: center;
}

.todo-input {
  flex: 1;
  height: 62px;
  border: 3px solid #FF7B67;
  border-radius: 16px;
  padding: 0 18px;
  font-size: 18px;
  outline: none;
  background: #ffffff;
}
.todo-input::-moz-placeholder {
  color: #aaa;
  font-weight: 600;
}
.todo-input::placeholder {
  color: #aaa;
  font-weight: 600;
}
.todo-input:focus {
  box-shadow: 0 0 0 4px rgba(255, 123, 103, 0.2);
}

.add-btn {
  width: 62px;
  height: 62px;
  border-radius: 14px;
  background: #FF7B67;
  color: #ffffff;
  cursor: pointer;
  display: grid;
  place-items: center;
}
.add-btn svg {
  font-size: 22px;
}
.add-btn:hover {
  filter: brightness(0.96);
}
.add-btn:active {
  transform: translateY(1px);
}

.delete {
  margin-top: 16px;
  display: flex;
  justify-content: center;
}
.delete button {
  width: 100%;
  height: 52px;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.6);
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
}
.delete button:hover {
  background: rgba(255, 255, 255, 0.8);
}

.todo-box {
  margin-top: 10px;
}

.todo-list {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.todo-item {
  height: 64px;
  padding: 0 16px;
  background: #ffffff;
  border: 3px solid #FF7B67;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: background 0.15s ease;
}
.todo-item:hover {
  background: #fff1ee;
}
.todo-item .left {
  display: flex;
  align-items: center;
  gap: 14px;
}
.todo-item .left .check {
  font-size: 22px;
  color: #9a9a9a;
  transition: color 0.15s ease, transform 0.1s ease;
}
.todo-item .left .text {
  font-size: 18px;
  font-weight: 600;
}
.todo-item .del-btn {
  padding: 8px;
  cursor: pointer;
}
.todo-item .del-btn svg {
  font-size: 20px;
  color: #111;
  transition: opacity 0.15s ease, transform 0.1s ease;
}
.todo-item .del-btn:hover svg {
  opacity: 0.7;
}
.todo-item .del-btn:active svg {
  transform: scale(0.95);
}
.todo-item.done {
  background: #f7f7f7;
}
.todo-item.done .left .check {
  color: #FF7B67;
}
.todo-item.done .left .text {
  color: #9a9a9a;
  text-decoration: line-through;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #111;
}
footer address {
  max-width: 720px;
  margin: 0 auto;
  padding: 18px 16px;
  font-size: 16px;
  font-style: normal;
  color: #ffffff;
}

@media (max-width: 480px) {
  header h2 {
    font-size: 30px;
  }
  .todo-item {
    height: 58px;
  }
  .todo-item .left .text {
    font-size: 16px;
  }
}/*# sourceMappingURL=todo.css.map */