$bg: #d9d9d9;
$white: #ffffff;
$black: #111;
$coral: #FF7B67;
$coral-soft: #fff1ee;
$gray: #9a9a9a;

$radius: 16px;


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
}

button {
  background: none;
  border: none;
}

body {
  font-family: "Noto Sans KR", system-ui, -apple-system, sans-serif;
  background: $bg;
  color: $black;
  min-height: 100vh;
}

.wrap {
  max-width: 720px;
  margin: 0 auto;
  padding: 48px 16px 120px;
}


h2 {
  text-align: center;
  font-size: 30px;
  font-weight: 800;
  margin-bottom: 30px;
}

.form {
  display: flex;
  gap: 14px;
  align-items: center;
}

.todo-input {
  flex: 1;
  height: 62px;
  border: 3px solid $coral;
  border-radius: $radius;
  padding: 0 18px;
  font-size: 18px;
  outline: none;
  background: $white;

  &::placeholder {
    color: #aaa;
    font-weight: 600;
  }

  &:focus {
    box-shadow: 0 0 0 4px rgba($coral, 0.2);
  }
}

.add-btn {
  width: 62px;
  height: 62px;
  border-radius: 14px;
  background: $coral;
  color: $white;
  cursor: pointer;

  display: grid;
  place-items: center;

  svg {
    font-size: 22px;
  }

  &:hover {
    filter: brightness(0.96);
  }

  &:active {
    transform: translateY(1px);
  }
}

.delete {
  margin-top: 16px;
  display: flex;
  justify-content: center;

  button {
    width: 100%;
    height: 52px;
    border-radius: 14px;
    background: rgba($white, 0.6);
    font-size: 16px;
    font-weight: 700;
    cursor: pointer;

    &:hover {
      background: rgba($white, 0.8);
    }
  }
}



.todo-box {
  margin-top: 10px;
}

.todo-list {
  display: flex;
  flex-direction: column;
  gap: 14px;
}


.todo-item {
  height: 64px;
  padding: 0 16px;
  background: $white;
  border: 3px solid $coral;
  border-radius: $radius;

  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  transition: background 0.15s ease;

  &:hover {
    background: $coral-soft;
  }

  .left {
    display: flex;
    align-items: center;
    gap: 14px;

    .check {
      font-size: 22px;
      color: $gray;
      transition: color 0.15s ease, transform 0.1s ease;
    }

    .text {
      font-size: 18px;
      font-weight: 600;
    }
  }

  .del-btn {
    padding: 8px;
    cursor: pointer;

    svg {
      font-size: 20px;
      color: $black;
      transition: opacity 0.15s ease, transform 0.1s ease;
    }

    &:hover svg {
      opacity: 0.7;
    }

    &:active svg {
      transform: scale(0.95);
    }
  }

  &.done {
    background: #f7f7f7;

    .left {
      .check {
        color: $coral;
      }

      .text {
        color: $gray;
        text-decoration: line-through;
      }
    }
  }
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #111;

  address {
    max-width: 720px;
    margin: 0 auto;
    padding: 18px 16px;
    font-size: 16px;
    font-style: normal;
    color: $white;
  }
}


@media (max-width: 480px) {
  header h2 {
    font-size: 30px;
  }

  .todo-item {
    height: 58px;

    .left .text {
      font-size: 16px;
    }
  }
}